.header {
  padding-top: 20px;
  padding: 20px 10px 0 10px;
  background: #484283;


  &__content {
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 20px;
  }

  &__right{
    display: flex;
    align-items: center;
    gap: 20px;
  }

}

.nav{
    display: flex;
    align-items: center;
    gap: 40px;

    &__item{
        font-weight: 350;
        font-size: 14px;
        line-height: 14px;
    }
}
