.product__item-buttons {
  display: flex;
  align-items: center;
  gap: 40px;
  position: absolute;
  top: 41%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 4;
  width: 100%;
  justify-content: center;
  height: 300px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 4px;
  opacity: 0;
  transition: .3s all ease;

  &:hover{
    opacity: 1;
  }
}
