.product__img {
  height: 293px;
  max-width: 280px;
  width: 100%;
  object-fit: cover;
  border-radius: 4px;
  background-size: cover !important;
  position: relative;

}

.product__item {
  max-width: 280px;
  width: 100%;
  position: relative;

  &-title {
    font-weight: 350;
    font-size: 16px;
    line-height: 16px;
    color: #444b58;
    margin-top: 10px;
  }

  &-price {
    font-weight: 350;
    font-size: 20px;
    line-height: 20px;
    color: #444b58;
    margin-top: 10px;
  }


}


