.modal__title {
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  color: #444b58;
}

.modal__content {
  background: #ffffff;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  padding: 30px;
  padding-top: 10px;
  margin-top: 40px;
}

.modal__info {
  display: flex;
  align-items: center;
  gap: 10px;

  span {
    font-weight: 700;
    font-size: 16px;
    line-height: 16px;
    color: #444b58;
  }

  p {
    font-weight: 350;
    font-size: 16px;
    line-height: 16px;
    color: #b2b5bb;
  }
}
