@import-normalize;

@font-face {
  font-family: 'intro';
  src: local('intro'), url('./Assets//Fonts//Intro-Book.woff2') format('woff2');
  font-weight: 350;
}

@font-face {
  font-family: 'intro';
  src: local('intro'), url('./Assets//Fonts//Intro-Regular.woff2') format('woff2');
  font-weight: 400;
}

@font-face {
  font-family: 'intro';
  src: local('intro'), url('./Assets//Fonts//Intro-Bold.woff2') format('woff2');
  font-weight: 700;
}

a{
  color: #fff;
  text-decoration: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: intro;
  box-sizing: border-box;
}

.container{
  max-width: 1180px;
  width: 100%;
  margin: 0 auto;
}


button{
  display: block;
  border: none;
  outline: none;
  cursor: pointer;
}




