.cart {
  padding: 10px 20px;
  height: 120px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__right{
    cursor: pointer;
  }

  &__left {
    display: flex;
    align-items: center;
    gap: 10px;

    img {
      max-width: 100px;
      width: 100%;
      object-fit: cover;
      height: 57px;
    }

    &__info {
      max-width: 268px;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      span {
        font-weight: 700;
        font-size: 20px;
        line-height: 20px;
        color: #4d4d4d;
      }

      p {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: #4d4d4d;
        margin: 0;
        margin-bottom: 14px;
      }
    }
  }
}
