.product {
  display: flex;
  gap: 20px;

  &__title {
    font-weight: 350;
    font-size: 24px;
    line-height: 140%;
    color: #444b58;
    margin-bottom: 20px;
  }

  &__descr {
    font-weight: 350;
    font-size: 16px;
    line-height: 140%;
    color: #444b58;
    margin-top: 20px;
  }

  &__genre__title {
    font-weight: 350;
    font-size: 16px;
    line-height: 16px;
    color: #444b58;
    margin-bottom: 20px;
  }

  &__genre {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-top: 20px;
    font-weight: 350;
    font-size: 16px;
    line-height: 16px;
    color: #444b58;
    margin-bottom: 40px;
  }

  &__price {
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    color: #444b58;
  }

  &__btn{
    margin-top: 40px;
  }

  &__left {
    max-width: 520px;
    width: 100%;

    span {
      font-weight: 400;
      font-size: 24px;
      line-height: 24px;
      color: #444b58;
    }
  }

  &__right {
    max-width: 430px;
    width: 100%;
  }
}
