.hero {
  padding: 100px 0;
  padding-top: 65px;
  position: relative;
  z-index: 5;
  background: #484283;

  &__content{
   position: relative;
   z-index: 3;
  }

  &__bg {
    position: absolute;
    top: 50%;
    left: 50%;
    font-weight: 700;
    font-size: 200px;
    line-height: 200px;
    color: rgba(255, 255, 255, 0.12);
    transform: translate(-50%, -50%);
    z-index: 1;
  }

  &__title {
    font-weight: 400;
    font-size: 36px;
    line-height: 140%;
    color: #ffffff;
    max-width: 580px;
    width: 100%;
  }

  &__desc {
    font-weight: 350;
    font-size: 16px;
    line-height: 140%;
    color: #ffffff;
    margin-top: 20px;
    max-width: 480px;
    width: 100%;
  }

  &__btn {
    margin-top: 40px;
  }
}
