.modal {
  background: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: 0.5s all ease;
  pointer-events: none;

  &__content {
    padding: 40px;
    background: #fff;
    transform: scale(0.5);
    transition: .5s all ease;
    min-width: 500px;
  }
}

.modal.active {
  opacity: 1;
  z-index: 100;
  pointer-events: all;
}

.modal__content.active{
  transform: scale(1);
}
