.noUi-horizontal {
  height: 4px;
}

.noUi-target {
  border: none;
  box-shadow: none;
  background: #444b58;
}

.noUi-connect {
  background: #444b58;
}

.noUi-horizontal .noUi-handle {
  top: -8px;
  width: 6px;
  height: 20px;
}

.noUi-handle {
  right: 0 !important;
  border: none;
  border-radius: 100px;
  box-shadow: none;
  background: #444b58;
  cursor: pointer;
}

.noUi-handle::after,
.noUi-handle::before {
  display: none;
}

.noUi-handle-lower {
  right: -0px;
}

.noUi-handle:focus {
  outline: none;
  background: #444b58;
}

.noUi-touch-area {
  right: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

.label__input-ui {
  display: flex;
  border: 1px solid #B2B5BB;
  border-radius: 4px;
  position: relative;

  &::before{
    content: '';
    position: absolute;
    height: 34px;
    width: 1px;
    background: #B2B5BB;
    top: 8px;
    left: 50%;
  }
}

.ui-input {
  width: 130px;
  height: 50px;
  background: transparent;
  border: none;
  outline: none;
  text-align: center;
  font-weight: 350;
  font-size: 16px;
  line-height: 16px;
  color: #444b58;
}
