.cart__block{
    position: relative;
    right: 8px;

    img{
        max-width: 20px;
        width: 100%;
        height: 18px;
        object-fit: cover;
        cursor: pointer;
    }

    &-total{
        width: 16px;
        height: 16px;
        background: #F14F4F;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        right: -8px;
        bottom: -5px;

        span{
            font-weight: 350;
            font-size: 14px;
            line-height: 14px;
            color: #fff;
        }
    }
}

