.cart__menu {
  position: absolute;
  background: #ffffff;
  z-index: 10;
  width: 480px;
  right: 0px;
  top: 45px;
  transition: .3s all ease;


  &-top{
    min-height: 330px;
    position: relative;
  }

}

.cart__menu-bottom {
  height: 91px;
  max-width: 440px;
  width: 100%;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #ffffff;
  box-shadow: 0px -4px 10px rgba(0, 13, 84, 0.1);

  &-left-total {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #4d4d4d;
    margin: 0;
    margin-bottom: 10px;
  }

  &-right{
    max-width: 240px;
    width: 100%;
  }
}

.total {
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  color: #4d4d4d;
}

.empty-cart{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 30px;
}



.menu-enter-active {
  transform: translateX(0);
  transition: .5s all ease-in-out;
}
.menu-exit {
  transform: translateX(0);
}
.menu-exit-active {
  transform: translateX(-350px);
  transition: .5s all ease-in-out;
}