.catalog {
  &__title {
    margin-top: 60px;
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    color: #444b58;
  }

  &__left {
    display: flex;
    flex-direction: column;
    max-width: 280px;
    width: 100%;
    padding: 20px;
    background: #fff4ee;
    border: 1px solid #fff4ee;
    border-radius: 4px;
    height: 597px;

    &-title {
      font-weight: 350;
      font-size: 24px;
      line-height: 24px;
      color: #444b58;
      max-width: 240px;
      width: 100%;
    }

    &-section-title {
      font-weight: 400;
      font-size: 16px;
      line-height: 16px;
      color: #444b58;
      margin-top: 20px;
      margin-bottom: 10px;
    }
  }

  &__content{
    display: flex;
    gap: 20px;
  }

  &__right{
    display: flex;
    max-width: 880px;
    width: 100%;
    flex-wrap: wrap;
  }
}

.delete__btn{
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: 20px;
    cursor: pointer;
  
}

table {
  border-left: 1px solid #dbbba9;
  border-right: 1px solid #dbbba9;
  border-top: 1px solid #dbbba9;
  border-radius: 1.14286px 0px 0px 0px;
  margin-bottom: 20px;


  td {
    width: 80px;
    height: 57px;
    text-align: center;
    cursor: pointer;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #444b58;
    transition: .3s all ease;

    &:hover{
        background: #DBBBA9;
    }

    &:nth-child(3n + 1) {
        border-right: 1px solid #DBBBA9;
        border-bottom: 1px solid #DBBBA9;
    }

    &:nth-child(3n + 2) {
        border-bottom: 1px solid #DBBBA9;
    }

    &:nth-child(3n + 3) {
        border-left: 1px solid #DBBBA9;
        border-bottom: 1px solid #DBBBA9;
    }
  }
}
